<template>
  <div id="home">
    <mt-swipe :auto="2000">
      <mt-swipe-item><img src="../../static/images/1.jpg" alt=""></mt-swipe-item>
      <mt-swipe-item><img @click="jumpInfo" src="../../static/images/2.jpg" alt=""></mt-swipe-item>
    </mt-swipe>
    <section>
      <div class="count_down">距离12月31日交房还剩</div>
      <div class="count_down_time"><span>{{date.day}}</span>天<span>{{date.hour}}</span>小时<span>{{date.minute}}</span>分钟<span>{{date.second}}</span>秒</div>
    </section>
    <img src="../../static/images/3.jpg" alt="" class="bigimg">
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      msg: "Welcome to HomePage",
      time:'00天00小时00分00秒',
      date:{
        day:'00',
        hour:'00',
        minute:'00',
        second:'00'
      }
    };
  },
  created(){
    let that = this;
    setInterval(()=>{
      this.countDownTime();
    },1000);
  },
  methods:{
    countDownTime(){
      //获取当前时间  
      var date = new Date();  
      var now = date.getTime();  
      //设置截止时间  
      var str="2019/1/1 00:00:00";
      var endDate = new Date(str); 
      var end = endDate.getTime();  
      //时间差  
      var leftTime = end-now; 
      //定义变量 d,h,m,s保存倒计时的时间  
      var d,h,m,s;  
      if (leftTime>=0) {  
          d = Math.floor(leftTime/1000/60/60/24);
          this.date['day'] = d<10?'0'+d:''+d;  
          h = Math.floor(leftTime/1000/60/60%24); 
          this.date['hour'] = h<10?'0'+h:''+h;   
          m = Math.floor(leftTime/1000/60%60);  
          this.date['minute'] = m<10?'0'+m:''+m;  
          s = Math.floor(leftTime/1000%60);
          this.date['second'] = s<10?'0'+s:''+s;           
      }else{
        this.date={
          day:'00',
          hour:'00',
          minute:'00',
          second:'00'
        }
      }
    },
    jumpInfo(){
      this.$router.push({name: 'Info',params:{}});
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.mint-swipe{
  height:2.5rem;
}
.mint-swipe img{
  width:100%;height: 100%;
}
.count_down{
  font-size: 0.22rem;text-align: center;
}
.count_down_time{
  font-size: 0.18rem;text-align: center;margin-top:0.1rem;
}
.bigimg{
  width:100%;
}
section{
  background: url('../../static/images/4.jpg') no-repeat;padding:0.2rem 0;color:#fff;background-size: 100%;
}
section span{
  color:red;font-weight: bold;
}
</style>



